body{
    background-color:beige;
    margin: 0;
    padding: 0;
    font-family: 'Edu NSW ACT Foundation', cursive;
}

.whoarewe{
    background: linear-gradient(90deg,rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.1) 100%) ,url("../img/diaz4.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    color: #000;
    min-height: 30rem;
    position: relative;
    display: table;
    width: 100%;
    height: auto;
    padding-top: 6rem;
    padding-bottom: 6rem;
    text-align: center;
}

.whoarewe-title{
    width: 80%;
    align-items: center !important;
    justify-content: center !important;
    align-content: center !important;
    text-align: center;
    padding: 0.25rem;
    margin-top: 0.65rem;
    margin-left: auto;
    margin-right: auto;
    background-color: rgba(128, 206, 214,0.6);
    color: #fff;
    border-radius: 10px 10px;
}

.whoarewe-content{
    position: relative;
    width: 60%;
    min-width: 300px;
    align-items: center !important;
    justify-content: center !important;
    align-content: center !important;
    text-align: center;
    padding: 0.85rem;
    margin-top: 1.65rem;
    margin-left: auto;
    margin-right: auto;
    color: #000;
    background-color:rgba(245, 245, 220,0.7);
    border-radius: 10px 10px;
}


.homepage{
    background: linear-gradient(90deg,rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.1) 100%) ,url("../img/diaz5.png");
