.page-section{
    padding-top: 6rem;
    padding-bottom: 6rem;
}

button{
    background-color: rgba(128, 206, 214,0.8);
    color: #fff;
    border-radius: 10px 10px;
    padding: 0.74rem;
    font-size: 18px;
}
button a{
    text-decoration: none;
}

button:hover{
    background-color: rgba(255, 255, 255,0.8);
    color: #000;
    cursor: pointer;
}

.row{
    padding: 0.35rem;
    padding-top: 0.45rem;
    display: flex;
    flex-direction: row;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
}
.row label{
    width: 30%;
    min-width: 100px;
    height: 26px;
    text-align: right;
    padding-right: 0.45rem;
}

.row input[type='text'], .row input[type='password'], .row input[type='email'] ,.row input[type='file'], .row input[type='number'], select, .row .radio {
    width: 60%;
    min-width: 150px;
    height: 26px;
    padding-right: 0.45rem;
    margin-right: auto;
    border: 1px solid rgba(208, 215, 216, 0.9);
    border-radius: 5px 5px;
}

.row textarea{
    width: 60%;
    min-width: 150px;
    height: 100px;
    padding-right: 0.45rem;
    margin-right: auto;
    border: 1px solid rgba(208, 215, 216, 0.9);
    border-radius: 5px 5px;
}

.row a{
    background-color: rgba(117, 137, 139, 0.8);
    color: #fff;
    border-radius: 10px 10px;
    padding: 0.24rem;
    margin-left: 0.55rem;
    font-size: 15px;
    text-decoration: none;
}

.row a:hover{
    background-color: rgba(255, 255, 255,0.8);
    color: #000;
    cursor: pointer;
}

.submit{
    background-color: rgba(128, 206, 214,0.8);
    color: #fff;
    border-radius: 10px 10px;
    padding: 0.34rem;
    font-size: 15px;
    text-decoration: none;
}

.submit a:hover{
    background-color: rgba(255, 255, 255,0.8);
    color: #000;
    cursor: pointer;
}
.action-view{
    background-color: rgba(156, 184, 187, 0.8);
    color: #fff;
    border-radius: 10px 10px;
    padding: 0.14rem;
    font-size: 9px;
    text-decoration: none;
    display: inline-block;
}

.action-view a:hover{
    background-color: rgba(255, 255, 255,0.8);
    color: #000;
    cursor: pointer;
}

.form-div{
    padding: 0.34rem;
    background-color: rgba(255, 255, 255,0.8);
    color: #000;
}

.row .radio-label{
    cursor: pointer;
    padding: 0.35rem;
}
